<template>
  <div class="search"v-bind:style="{ 'background-image': 'linear-gradient(to right, #a517ba,#5f1782);'}">
    <h2>NASA Image Search</h2>
    <form v-on:submit.prevent="getResult(query)">
      <input type="text" placeholder="Type in your search" v-model="query"/>
    </form>
    <div class="results" v-if="results">

      <div v-for="result in results">
        <img v-bind:src="result.links[0].href"/>

      </div>

    </div>
    
  </div>
</template>

<script>
import { METHODS } from 'http';
import axios from 'axios';
export default {
  name: 'search',
  data () {
    return {
      msg: 'Search',
      query: '',
      results: ''
    }
  },
  methods: {
    getResult(query){
      axios.get('https://images-api.nasa.gov/search?q=' + query + '&media_type=image').then( response => {
        console.log(response.data.collection.items);
        this.results = response.data.collection.items;
      })
    }
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body{
  background-color: #a517ba;
}
input{
  padding: 10px;
  border-radius: 10px;
  font-size: 1rem;
}
.results img{
  height: 300px;
  margin: 10px;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



